    import appAd from '../../../components/page-component/app-ad/app-ad';
    import {mapGetters, mapState} from "vuex";

    export default {
        data() {
            return {
                showShare: false,
                user_id: 0,
                share_title: '',
                setting: {},
                first: false,
                activity_data: {},
                ad_data: {},
                page: 1,
                template_remind: '',
                template_activity: '',
                banner_list: {},
                toExchange: false,
                authorize: false,
                exchangNum: 0,
                user_data: {
                    daily_num: 0
                },
                goods: [],
                loading: false,
                invite_list: [{
                        avatar: './../image/friend.png'
                    },
                    {
                        avatar: './../image/friend.png'
                    },
                    {
                        avatar: './../image/friend.png'
                    },
                    {
                        avatar: './../image/friend.png'
                    }
                ],
                coupon_url: this.$api.step.receive,
				disable: 'disable'
            }
        },
        components: {
            appAd
        },
        computed: {
            ...mapState({
                stepImg: state => state.mallConfig.plugin.step,
                appImg: state => state.mallConfig.__wxapp_img.mall,
                appSetting: state => state.mallConfig.mall.setting,
                userInfo: state => state.user.info,
                platform: function(state) {
                    return state.gConfig.systemInfo.platform;
                }
            }),
             ...mapGetters('mallConfig', {
                getVideo: 'getVideo',
                getTheme: 'getTheme'
             }), 
            ...mapGetters('iPhoneX', {
                getBoolEmpty: 'getBoolEmpty'
            })
        },
        filters: {
            getObje(data, temp) {
                return {
                    id: data.id
                }
            }
        },
        methods: {
            showHiddenClick() {
                this.showShare = !this.showShare;
            },

            toIndex() {
                uni.redirectTo({
                    url: '/pages/index/index'
                });
            },

            toDetail() {
                uni.navigateTo({
                    url: '/plugins/step/detail/detail'
                });
            },

            toShare(num) {
                this.showShare = false;
                uni.navigateTo({
                    url: '/plugins/step/share/share?step=' + num
                });
            },

            toRules() {
                // uni.navigateTo({
                //     url: '/plugins/step/rules/rules?rule=1'
                // });
                uni.navigateTo({
                    url: `/pages/rules/index?url=${encodeURIComponent(this.$api.step.setting)}&key=rule`,
                });
            },

            toTop() {
                uni.navigateTo({
                    url: '/plugins/step/top/top'
                });
            },

            toFriend() {
                uni.navigateTo({
                    url: '/plugins/step/friend/friend'
                });
            },

            toDare(id,num) {
                if(id && num) {
                    uni.navigateTo({
                        url: '/plugins/step/dare/dare?submit=1&activity_id=' + id +'&step_num=' + num
                    });
                }else {
                    uni.navigateTo({
                        url: '/plugins/step/dare/dare'
                    })
                }
            },

            toJoin(id) {
                uni.navigateTo({
                    url: '/plugins/step/join/join?id=' + id
                });
            },

            toGoods(data) {

                if (data.video_url && this.getVideo == 1) {
                    uni.navigateTo({
                        url: `/pages/goods/video?goods_id=${data.goods_id}&sign=step`
                    });
                } else {
                    uni.navigateTo({
                        url: '/plugins/step/goods/goods?goods_id=' + data.goods_id
                    });
                }












            },
            getAuth() {
                let that = this;
                uni.getSetting({
                    success(res) {
                        if (res.authSetting['scope.werun'] == true) {
                            that.page = 1;
                            that.runData();
                            that.getGoods();
                        } else {
                            uni.authorize({
                                scope: 'scope.werun',
                                success(res) {
                                    if (res.errMsg == "authorize:ok") {
                                        that.page = 1;
                                        // 读取数据
                                        that.runData();
                                        that.getGoods();
                                    }
                                },
                                fail(res) {
                                    that.$hideLoading();
                                    uni.showModal({
                                        title: '提示',
                                        content: '获取步数信息失败，需要授权获取步数权限',
                                        showCancel: false,
                                        confirmText: '打开授权',
                                        success(e) {
                                            if (e.confirm) {
                                                uni.openSetting({});
                                            }
                                        }
                                    });
                                }
                            });
                        }
                    }
                })
            },
            aliAuth(today) {
                let that = this;
                let inviteList = [{
                        avatar: './../image/friend.png'
                    },
                    {
                        avatar: './../image/friend.png'
                    },
                    {
                        avatar: './../image/friend.png'
                    },
                    {
                        avatar: './../image/friend.png'
                    }
                ]
                if(that.user_id == 0 && that.userInfo) {
                    that.user_id =  that.userInfo.options.user_id;
                }
                my.getRunData({
                    countDate: today,
                    success: (res) => {
                        that.$request({
                            url: that.$api.step.index,
                            data: {
                                encrypted_data: res.response,
                                parent_id: that.user_id,
                                page: 1
                            },
                            method: 'POST',
                        }).then(response=>{
                            that.$hideLoading();
                            if (response.code == 0) {
                                that.template_remind = response.data.template_remind;
                                that.template_activity = response.data.template_activity;
                                that.activity_data = response.data.activity_data;
                                that.ad_data = response.data.ad_data;
                                that.banner_list = response.data.banner_list;
                                that.user_data = response.data.user_data;
                                that.invite_list = that.user_data.invite_list.concat(inviteList).slice(0, 4);
                                that.first = true;
                            } else {
                                uni.showToast({
                                    title: response.msg,
                                    icon: 'none',
                                    duration: 1000
                                });
                            }
                        }).catch(response  => {
                            that.$hideLoading();
                            that.$event.on(that.$const.EVENT_USER_LOGIN).then(()=>{
                                that.aliAuth(today);
                            });
                        });
                    },
                    fail: (res) => {
                        uni.showModal({
                            content: res.errMsg,
                            showCancel: false,
                        });
                    }
                });
            },

            runData() {
                let that = this;
                let code, iv, encrypted_data;
                let inviteList = [{
                        avatar: './../image/friend.png'
                    },
                    {
                        avatar: './../image/friend.png'
                    },
                    {
                        avatar: './../image/friend.png'
                    },
                    {
                        avatar: './../image/friend.png'
                    }
                ]
                uni.login({
                    success(res) {
                        if(that.user_id == 0 && that.userInfo) {
                            that.user_id =  that.userInfo.options.user_id;
                        }
                        code = res.code;
                        // 获取iv和encryptedData
                        uni.getWeRunData({
                            success(res) {
                                iv = res.iv;
                                encrypted_data = res.encryptedData;
                                that.$request({
                                    url: that.$api.step.index,
                                    data: {
                                        encrypted_data: encrypted_data,
                                        iv: iv,
                                        parent_id: that.user_id,
                                        code: code,
                                        page: 1
                                    },
                                    method: 'POST',
                                }).then(response=>{
                                    that.$hideLoading();
                                    if (response.code == 0) {
                                        that.template_remind = response.data.template_remind;
                                        that.template_activity = response.data.template_activity;
                                        that.activity_data = response.data.activity_data;
                                        that.ad_data = response.data.ad_data;
                                        that.banner_list = response.data.banner_list;
                                        that.user_data = response.data.user_data;
                                        that.invite_list = that.user_data.invite_list.concat(inviteList).slice(0, 4);
                                        that.first = true;
                                    } else {
                                        uni.showToast({
                                            title: response.msg,
                                            icon: 'none',
                                            duration: 1000
                                        });
                                    }
                                }).catch(response  => {
                                    that.$hideLoading();
                                    that.$event.on(that.$const.EVENT_USER_LOGIN).then(()=>{
                                        that.runData();
                                    });
                                });
                            },
                            fail(res) {
                                if (res.errMsg == 'getWeRunData:fail cancel') {
                                    uni.showModal({
                                        content: '读取失败，请稍后再试',
                                        showCancel: false,
                                    });
                                } else if (res.errMsg == 'getWeRunData: fail device not support') {
                                    uni.showModal({
                                        content: '请在微信中搜索"微信运动"公众号，并点击关注',
                                        showCancel: false,
                                    });
                                } else {
                                    uni.showModal({
                                        content: res.errMsg,
                                        showCancel: false,
                                    });
                                }

                            }
                        })
                    }
                })
            },
            getGoods() {
                let that = this;
                that.$request({
                    url: that.$api.step.goods,
                    data: {
                        page: that.page
                    },
                }).then(response=>{
                    if(response.code == 0) {
                        that.goods = that.goods.concat(response.data.list);
                        that.page++;
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    that.$hideLoading();
                });
            },
            refresh() {
                this.$showLoading({
                    type: 'global',
                    text: '步数加载中...'
                });

                this.runData();












            },
            openMsg() {
                if(this.user_data.is_remind == 0) {
                    this.$subscribe(this.template_remind);
                }
            },
            toSwitch(e) {
                let checked = 0;
                if (e.detail.value) {
                    checked = 1
                }
                let that = this;
                this.$request({
                    url: this.$api.step.remind,
                    data: {
                        is_remind: checked
                    },
                    method: 'post',
                }).then(response => {
                    if (response.code == 0) {
                        this.user_data.is_remind = checked;
                    }
                }).catch(response => {
                });
            },
            exchange() {
                let that = this;
                let convert_ratio = +that.setting.convert_ratio;
                let daily_real_num = +that.user_data.daily_real_num;
                let daily_num = +that.user_data.daily_num;
                let num = 0;
                if (convert_ratio > 0) {
                    num = (daily_num / convert_ratio).toFixed(2);
                }
                if (daily_num < 1 || num < 0.01) {
                    uni.showToast({
                        title: '可兑换步数不足',
                        icon: 'none',
                        duration: 1000
                    });
                } else {
                    that.toExchange = true;
                    that.exchangNum = num;
                    that.$forceUpdate();
                }
            },
            subscribe() {
                if(this.loading) {
                    return false;
                }
                this.loading = true;
                this.$subscribe(this.template_remind).then(res => {
                    this.exchangeSubmit();
                }).catch(res => {
                    this.exchangeSubmit();
                });
            },
            exchangeRequest(num, code, encrypted_data, iv) {
                let that = this;
                that.$request({
                    url: that.$api.step.convert,
                    data: {
                        num: num,
                        code: code,
                        encrypted_data: encrypted_data,
                        iv: iv,
                    },
                    method: 'post',
                }).then(response => {
                    that.loading = false;
                    uni.hideLoading();
                    if (response.code == 0) {
                        uni.showToast({
                            title: "兑换成功",
                            icon: 'success',
                            duration: 1000
                        });
                        setTimeout(function(){

                            that.runData();












                        },1000)
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    that.loading = false;
                    uni.hideLoading();
                });
            },
            exchangeSubmit() {
                let that = this;
                let num = this.user_data.daily_real_num;
                let code, iv, encrypted_data;
                that.toExchange = false;










                uni.showLoading({
                    mask: true,
                    title: '兑换中...'
                });
                uni.login({
                    success(res) {
                        code = res.code;
                        // 获取iv和encryptedData


                        uni.getWeRunData({
                            success(res) {
                                iv = res.iv;
                                encrypted_data = res.encryptedData;
                                that.exchangeRequest(num, code, encrypted_data, iv);
                            },
                            fail(res) {
                                that.loading = false;
                                uni.hideLoading();
                                uni.showModal({
                                    content: res.errMsg,
                                    showCancel: false,
                                });
                            }
                        })






























                    },
                    fail(res) {
                        that.loading = false;
                        uni.hideLoading();
                        uni.showModal({
                            content: res.errMsg,
                            showCancel: false,
                        });
                    }
                })
            },
        },
        onReachBottom() {
            this.getGoods();
        },

        onShareTimeline() {
          return this.$shareTimeline({
            title: this.share_title ? this.share_title : this.$children[0].navigationBarTitle,
            query: {} // 此处填写页面的参数
          });
        },

        onLoad(options) { this.$commonLoad.onload(options);
            let that = this;

            wx.showShareMenu({
              menus: ['shareAppMessage', 'shareTimeline']
            })

            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });













            this.$store.dispatch('user/info');
            if(options.user_id > 0) {
                that.user_id = options.user_id;
            }
            that.$request({
                url: that.$api.step.setting,
            }).then(response=>{

                that.getAuth();













                if(response.code == 0) {
                    that.setting = response.data;
                    that.share_title = response.data.share_title.toString();
                    that.rules = response.data.rule;
                    if(response.data.share_title.indexOf("\n") > -1) {
                        let share_title = response.data.share_title.split("\n");
                        let index = Math.floor(Math.random() * share_title.length);
                        that.share_title = share_title[index]
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        onShow() {
            let that = this;
            if (that.first == false) {
                return
            } else {

                that.runData();












            }
        },

        onShareAppMessage() {
            return this.$shareAppMessage({
                title: this.share_title ? this.share_title : this.$children[0].navigationBarTitle,
                imageUrl: this.setting.share_pic ? this.setting.share_pic : '',
                path: "/plugins/step/index/index",
                params: {
                    user_id: this.userInfo.options.user_id
                }
            });
        }

    }
